.post-details {
  padding: 10px 12px 0 10px;
  background-color: #f5f5f5;
  margin: 5px 0;
}

.post-details-item {
  position: relative;
  display: inline-block;
  width: 320px;
  vertical-align: top;
  margin-bottom: 15px;
}



.slideshow-container {
  max-width: 1000px;
  position: relative;
  margin: auto;
  text-align: center;
  height: 430px;
}

.mySlides {
  display: none;
}

.prev, .next {
  cursor: pointer;
  position: absolute;
  top: 50%;
  width: auto;
  margin-top: -22px;
  padding: 16px;
  color: white;
  font-weight: bold;
  font-size: 18px;
  transition: 0.6s ease;
  border-radius: 0 3px 3px 0;
  user-select: none;
}

.next {
  right: 0;
  border-radius: 3px 0 0 3px;
  background-color: rgba(0,0,0,0.8);
}

.prev{
  left: 0;
  background-color: rgba(0,0,0,0.8);
}

.prev:hover, .next:hover {
  background-color: rgba(0,0,0,0.8);
}


.numbertext {
  color: #f2f2f2;
  font-size: 12px;
  padding: 8px 12px;
  position: absolute;
  top: 0;
}


.fade {
  animation-name: fade;
  animation-duration: 1.5s;
}

@keyframes fade {
  from {opacity: .4}
  to {opacity: 1}
}
.fa-trash:before{

  content: "\f1f8";
  color: red;
  font-size:25px;
}